<template>
    <div>
        <tou/>
        <input type="text" v-model="txt" style="width:80%;height:1rem" placeholder="请输入地址"/>
        <van-button type="primary" @click="faA()" >确定</van-button>
        <div v-for="item in list" @click="faB(item)">
            <h3>{{item.name}}</h3>
            <p>{{item.address}}</p>
        </div>
    </div>
</template>
<script>
import tou from '../zhuye/toubiao'
export default {
    components:{
        tou,
    },
    data(){
        return{
            txt:'',
            list:[]

        }
    },
    methods:{
      faA(){
          this.axios.get(`http://elm.cangdu.org/v1/pois?type=nearby&keyword=`+this.txt).then((e)=>{
              this.list=e.data
          })
      },
      faB(item){
          this.$router.push({name:'shouhuo',query:{obj:item}})
      }
    },
    mounted(){
        localStorage.zhu='搜索地址'
    }
}
</script>


